        url('ifc-fonts/GTAmerica-Light.woff') format('woff');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'GT America';
    src: url('ifc-fonts/GTAmerica-Black.woff2') format('woff2'),
        url('ifc-fonts/GTAmerica-Black.woff') format('woff');
    font-weight: 900;
    font-style: normal;
}

@font-face {
    font-family: 'GT America';
    src: url('ifc-fonts/GTAmerica-LightItalic.woff2') format('woff2'),
        url('ifc-fonts/GTAmerica-LightItalic.woff') format('woff');
