    font-style: normal;
}

@font-face {
    font-family: 'GT America';
    src: url('ifc-fonts/GTAmerica-RegularItalic.woff2') format('woff2'),
        url('ifc-fonts/GTAmerica-RegularItalic.woff') format('woff');
